@import "./reset";
@import "./variables";


@font-face{
    font-family: f1;
    src: url("/font/segoescb.ttf");
}

@font-face{
    font-family: f2;
    src: url("/font/JosefinSans-Regular.ttf");
}
@font-face{
    font-family: f2;
    src: url("/font/JosefinSans-Bold.ttf");
    font-weight: bold;
}


html, body{
    font-family: f2, 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    overflow-x: hidden;    
}

h1{
    font-family: f1, 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 1.6em;
}
h2{
    font-family: f1, 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 1.5em;
}
h3{
    font-size: 1.4em;
}
h4{
    font-size: 1.3em;
}
h5{
    font-size: 1.2em;
}
h6{
    font-size: 1.1em;
}
strong{
    font-weight: bold;
    color: $color_5;
}
input, textarea{
    font-family: f2, 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}


.btn{
    padding: 1em;
    border-style: solid;
    border-color: $color_7;
    border-width: 1px;
    border-radius: $borderRadius;
    cursor: pointer;

    text-decoration: none;
    color: $color_6;
    
    box-shadow: 0 0 0 rgba(0, 0, 0, 0);
    background-color: $color_2;
    
    transition-property: background-color, box-shadow;
    transition-duration: $animTime;

    &:hover{
        box-shadow: 0 0 1em $color_6;
        background-color: $menuButtonHoverColor;
    }
}



#page{
    background-color: $bodyColor;
    min-height: calc(100vh - #{$desktopNavHeight});

    a{
        color: $darkFont;
        text-decoration: none;
    }

    #pageContent{
        box-sizing: border-box;
        width: 100%;
        
        padding: 1em;
        padding-top: 0;
                
        ul{
            list-style-type: initial;
        }
        ol{
            list-style-type: decimal;
        }
        .unit{
            width: 100%;
            max-width: 30em;
            margin: auto;
            text-align: justify;
            line-height: 2em;
            &.double{
                max-width: 60em;
            }
            img{
                width: 100%;
                border-radius: $borderRadius;
                background-color: white;
                box-shadow: 0 0 1em $shadowColor;
            }
            video {
                width: 100%;
                height: auto;
            }
        
            @media screen and (min-width: 980px){
                &.left{
                    position: relative;
                    left: 10em;
                }
                &.right{
                    position: relative;
                    right: 10em;
                }
            }
        }
        
        .grp{
            @media screen and (min-width: 980px){        
                display: flex;
                justify-content: center;
                
                .unit{
                    margin: 0 1em;
                }
            }
        }
    }
}




main{
    position: relative;
    left: 0;

    transition-property: left;
    transition-duration: $animTime;
    
    &.activeMobileMenu{
        left: $mobileNavWidth;

        #mobileMenuButton{
            background-image: url("/Images/site/close.svg") !important;
            right: $mobileNavWidth !important;
        }

        #mobileNav{
            left: 0;

            #blocker{
                display: block;
            }
        }
    }
    
    @media screen and (min-width: $mobileWidth){
        position: static;
    }    
    
    &.home{
        margin-top: calc(100vh - #{$desktopNavHeight});
        
        nav{
            position: sticky;
        }
    }

}


header{
    position: fixed;
    top: 0;
    width: 100%;
    // height:calc(100vh - #{$desktopNavHeight});
    height: 100vh;
    z-index: -1;
    
    background-image: url("/Images/project/akropol_001.jpeg");
    background-size: cover;
    background-position: center;
    display: none;

    &.home{
        display: initial;
    }
}