@import "../variables.scss";

#mobileNav{
    position: fixed;
    left: -$mobileNavWidth;
    top: 0;

    width: $mobileNavWidth;
    height: 100vh;
    
    transition-duration: $animTime;
    
    #panel{
        position: absolute;
        left: 0;
        
        width: $mobileNavWidth;
        height: 100vh;

        background-color: $color_1;
        padding: 2em;
        
        a{
            text-decoration: none;
            color: $color_6;
        }
        
        ul#lv1_1{
            .navBtn{
                padding: 1em;
                
                text-align: center;
                cursor: pointer;
                
                border-bottom-style: solid;
                border-width: 0.3em;
                border-color: $color_0;
                
                &:hover{
                    border-color: $color_6;
                }
                &.active{
                    border-color: $color_7;   
                }
            }
        }
        ul#footer{
            position: absolute;
            bottom: 0;
            margin-bottom: 2em;

            .social {
                display: flex;
                // width: 100%;
                li {
                    flex-shrink: 0;
                    width: 2.8em;
                    height: 2.8em;
        
                    border-radius: 1.5em;
                    border-top-right-radius: 0.2em;
        
                    margin: 0.25em;
                    background-color: $color_6;
                    background-size: cover;
                    background-position: center;
                    border-width: 1px;
                    border-style: solid;
                    border-color: $color_6;
        
                    box-shadow: 0 0 0 rgba(0, 0, 0, 0);
        
                    transition-property: box-shadow;
                    transition-duration: $animTime;
        
                    &:hover {
                        box-shadow: 0 0 1em $color_6;
                    }
                }
                .facebook {
                    background-image: url("/Images/site/facebook.svg");
                }
                .twitter {
                    background-image: url("/Images/site/twitter.svg");
                }
                .whatsapp {
                    background-image: url("/Images/site/whatsapp.svg");
                }
                .linkedin {
                    background-image: url("/Images/site/linkedin.svg");
                }
                .github {
                    background-image: url("/Images/site/github.svg");
                }
                .cv {
                    width: 100%;
                    border-radius: 1.5em;
                    border-top-right-radius: $borderRadius;
                    height: 3em;
                    margin: 0.25em;
        
                    text-align: center;
                }
                .space {
                    width: 100%;
                }
            }
        }
    }
    
    #blocker{
        display: none;
        position: fixed;
        left: 0; top: 0;

        background-color: rgba(0, 0, 0, 0.637);
        
        width: 100vw;
        height: 100vh;        
    }
}