@import "../variables.scss";

nav#desktopNav{
    z-index: 1;

    position: sticky;
    top: 0;
    width: 100%;
    color: $color_6;
    
    
    ul#base{
        display: flex;
        height: $desktopNavHeight;
        width: 100%;
        align-items: center;
        background-color: $color_1;
        padding: 0 1em;

        a{
            text-decoration: none;
            color: $color_6;
        }
        
        #logo{
            width: 4em; height: 4em;
            border-radius: 50%;
    
            background-image: url("/Images/site/favicon.jpg");
            background-size: cover;
            background-position: center;
        }
        #siteName{
            margin-left: 1em;
        }
        .gap{
            width: 100%;
        }


        ul#lv1{
            display: none;
            
            @media screen and (min-width: $mobileWidth){        
                display: flex;
            }
            
            
            .navBtn{
                position: relative;
                text-align: center;
                width: 7em;
                height: $desktopNavHeight;
                
                cursor: pointer;
                
                border-bottom-style: solid;
                border-width: 0.3em;
                border-color: $color_0;
                
                &.active{
                    border-color: $color_7;   
                }
                
                display: flex;
                flex-direction: column;
                justify-content: center;
                
                transition-duration: $animTime;
                
                &:hover{
                    border-color: $color_6;
                    .lv2{
                        display: block;
                    }
                }
                
                .lv2{
                    display: none;
                    position: absolute;
                    right: -1em;
                    
                    padding: 1em;
                    background-color: $menuButtonHoverColor;
                    
                    top: 5em;
                    bottom: initial;
                    &.up{
                        top: initial;
                        bottom: 3.5em;
                    }
                    .navBtn{
                        height: 3.5em;
                        border-color: $color_0;
                        
                        transition-duration: $animTime;
                        
                        &:hover{
                            border-color: $color_6;
                        }
                    }
                }
            }
        }
        
        
        #mobileMenuButton{
            position: relative;
            right: 0;

            width: 3em; height: 3em;
            background-image: url("/Images/site/bars.svg");
            background-repeat: no-repeat;
            background-position: center;
            background-size: 1em;
            
            transition-duration: $animTime;
            cursor: pointer;

            @media screen and (min-width: $mobileWidth){        
                display: none;
            }
        }
    }
}
